import React from 'react'
import { PureComponent } from 'react'
import { ReactElement } from 'react'
import { RefObject } from 'react'

import { SubjectSummary } from '../components/subject/SubjectSummary'
import { SubjectContent } from '../components/subject/SubjectContent'
import { SubjectTitle } from '../components/subject/SubjectTitle'
import { SubjectKeyword } from '../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../components/subject/SubjectIdentifier'
import { Subject } from '../components/subject/Subject'


export class Map extends PureComponent {
  private _mapRoot : RefObject<HTMLDivElement>

  public constructor (props : any, context : any) {
    super(props, context)

    this._mapRoot = React.createRef()
  }

  public componentDidMount () : void {
    if (window != undefined) {
      const leaflet : any = require('leaflet')

      console.log(this._mapRoot.current)
      const map : any = leaflet.map(this._mapRoot.current, {
        crs: leaflet.CRS.Simple
      })
    }
  }

  public render () : ReactElement {
    return (
      <Subject>
        <SubjectIdentifier>map</SubjectIdentifier>
        <SubjectKeyword>Map</SubjectKeyword>
        <SubjectTitle>Monde</SubjectTitle>
        <SubjectSummary>

        </SubjectSummary>
        <SubjectContent>
          <div className='map' ref={this._mapRoot} />
        </SubjectContent>
      </Subject>
    )
  }
}
